/* eslint-disable no-undef */
const modal = document.querySelector('.menu__modal');
const modalOverlay = document.querySelector('.menu__modal-overlay');
const modalContent = document.querySelector('.menu__modal-content');

let isModalOpen = false;

export const toggleModal = () => {
  if (isModalOpen) {
    modal.classList.remove('modal--visible');
    modalOverlay.classList.remove('modal__overlay--visible');
    modalContent.classList.remove('modal__content--visible');
    document.body.classList.remove('stop-scroll');
    isModalOpen = false;
  } else {
    modal.classList.add('modal--visible');
    modalOverlay.classList.add('modal__overlay--visible');
    modalContent.classList.add('modal__content--visible');
    document.body.classList.add('stop-scroll');
    isModalOpen = true;
  }
};

document.addEventListener('click', (e) => {
  if (!e.target.closest('.menu__modal-content') && !e.target.closest('.menu__list-item') && modalContent.classList.contains('modal__content--visible')) toggleModal();
});

export default toggleModal;
